import Api from '../../api/user';
	
 export default {
        data() {
            return {
				loginForm: {  
				    workNo: "05360",  
				    password: "123456",  
				}, 
            }
        },
        methods: {
            login: function(e) {
				Api.login(e.detail.value)
				.then(res => {
					console.log(res),
					uni.setStorageSync('token', res.data.token);
					uni.switchTab({
						url: '/pages/info/info'
					});
				})
            }
        }
    }
